import { Injectable } from '@angular/core';
import { BaseInputDataService } from '../../constants/input-data/base-input-data.service';
import { Stock, PieSet, PieDataSet } from '../../models/stock-datamodels';
import { Colorpallet, Tagtype } from '../../constants/enums';
import { enumValuesToListUtil } from 'src/app/shared/utils/enum-utils';
import { TagUtilService } from '../tag-util-service/tag-util.service';

@Injectable({
  providedIn: 'root'
})
export class StockUtilService {
  BACKGROUNDCOLOR: string[] = enumValuesToListUtil(Colorpallet);
  constructor(private baseDataServce: BaseInputDataService, private tagUtil: TagUtilService) { }

  getCountries(): Stock[] {
    return this.baseDataServce.getBaseStockData();
  }

  getTagPieSet(tagtype: Tagtype): PieSet {
    const labelNames = this.baseDataServce.getTagsData().filter(a => a.tagType === tagtype).map(a => a.tagName);
    const data = this.getProcentOfEachTagNames(labelNames);
    labelNames.push('Other');
    data.push(this.getProcentWithNoTagOfType(tagtype));
    const colors = this.BACKGROUNDCOLOR.slice(0, labelNames.length);
    return this.createPieSet(data, colors, labelNames);
  }

  getProcentOfEachTagNames(tagNames: string[]): number[] {
    const procents = new Array<number>();
    tagNames.forEach(tagName => {
      procents.push(this.getProcentOfTagName(tagName));
    });
    return procents;
  }

  getProcentOfTagName(tagName: string): number {
    let procent = 0;
    this.baseDataServce.getBaseStockData().filter(stock => stock.tags.includes(tagName))
            .forEach(stock => procent += stock.procentageOfPortfolio);
    return procent;
  }

  createPieSet(data, colors, labelNames): PieSet {
    const pieSet: PieSet = new PieSet();
    const pieDataSet = new PieDataSet();
    pieDataSet.data = data;
    pieDataSet.backgroundColor = colors;
    pieSet.datasets = new Array(pieDataSet);
    pieSet.labels = labelNames;
    return pieSet;
  }

  getProcentWithNoTagOfType(tagtype: Tagtype): number {
    let procent = 0;
    this.baseDataServce.getBaseStockData().filter(stock => !this.tagUtil.containsTagOfType(stock.tags, tagtype))
            .forEach(stock => {
              procent += stock.procentageOfPortfolio;
            });
    return procent;
  }

  private getPieDataSetByTag(tagtype: Tagtype): number[] {
    const myData = new Array<number>();
    this.baseDataServce.getTagsData().forEach(tagData => {
      const procentOfPortfolio = this.sumAllStocksWithSertainTag(tagtype, this.baseDataServce.getBaseStockData(), tagData.tagName);
      if (procentOfPortfolio > 0) {
        myData.push(procentOfPortfolio);
      }
    });
    return myData;
  }

  private sumAllStocksWithSertainTag(tagType: Tagtype, stocks: Stock[], tagName: string): number {
    let returnValue = 0;
    stocks.forEach(stock => {
      if (this.tryExtractTagByType(stock.tags, tagType) === tagName) {
        returnValue += stock.procentageOfPortfolio;
        console.log('adding ' + stock.procentageOfPortfolio);
      }
    });
    return returnValue;
  }

  private tryExtractTagByType(tagNames: string[], lookingForTagType: Tagtype): string {
    let s = '';
    tagNames.forEach(nameKey => {
      if (this.tagUtil.findTagTypeByName(nameKey) === lookingForTagType) {
        s = nameKey;
      }
    });
    return s;
  }

  getBasicStockPieSet(): PieSet {
    let labelNames = new Array<string>();
    const pieSet: PieSet = new PieSet();
    labelNames = this.baseDataServce.getBaseStockData().map(a => a.name);
    pieSet.datasets = this.getBasicStockPieData();
    pieSet.labels = labelNames;
    return pieSet;
  }

  getBasicStockPieData(): PieDataSet[] {
    let myData = new Array<number>();

    myData = this.baseDataServce.getBaseStockData().map(a => a.procentageOfPortfolio);
    const pieDataSet = new PieDataSet();
    pieDataSet.data = myData;
    pieDataSet.backgroundColor = this.BACKGROUNDCOLOR.slice(0, myData.length);

    return new Array(pieDataSet);
  }
}
