import { Component, Output, EventEmitter, AfterViewInit } from '@angular/core';
import { Tagtype } from 'src/app/core/constants/enums';

interface SeletCategory {
  value: Tagtype;
  viewValue: string;
}

@Component({
  selector: 'app-dropdown-select',
  templateUrl: './dropdown-select.component.html',
  styleUrls: ['./dropdown-select.component.css']
})
export class DropdownSelectComponent implements AfterViewInit {
  @Output() customChange = new EventEmitter<Tagtype>();
  selects: ['Based on Size', 'Based on Sector', 'Based on Risk', 'Based on Stocks' ];
  selected = Tagtype.STOCK;
  foods: SeletCategory[] = [
    {value: Tagtype.STOCK, viewValue: 'Stock'},
    {value: Tagtype.SECTOR, viewValue: 'Sector'},
    {value: Tagtype.SIZE, viewValue: 'Size'},
    {value: Tagtype.RISK, viewValue: 'Risk'}
  ];
  constructor() { }
  ngAfterViewInit(): void {
    this.customChange.emit(this.selected);
  }

  setValue() {
    this.customChange.emit(this.selected);
  }
}
