import { Component, OnInit, AfterViewInit, ElementRef, ViewChild } from '@angular/core';
import { Chart } from 'chart.js';
import { StockUtilService } from '../../core/services/stock-util-service/stock-util.service';
import { Tagtype } from 'src/app/core/constants/enums';

@Component({
  selector: 'app-homepage',
  templateUrl: './main-chart.component.html',
  styleUrls: ['./main-chart.component.scss']
})
export class HomepageComponent implements AfterViewInit {

  @ViewChild('myPieChart') myPieChart!: ElementRef<HTMLCanvasElement>;
  chart!: Chart;
  selected: Tagtype;
  constructor(private stockUtilService: StockUtilService) {}

  onSelectChange(eventArgs: Tagtype) {
    this.selected = eventArgs;
    this.uppdateChart();
  }

  ngAfterViewInit() {
    this.uppdateChart();
  }

  uppdateChart() {
    this.resetChartIfNeeded();
    let pieData;
    if (this.selected === Tagtype.STOCK) {
      pieData = this.stockUtilService.getBasicStockPieSet();
    } else { pieData = this.stockUtilService.getTagPieSet(this.selected);
    }

    this.chart = new Chart(this.myPieChart.nativeElement, {
      type: 'pie',
      data: pieData,
      options: {
        aspectRatio: 1,
      }
    });
  }

  private resetChartIfNeeded() {
    if (this.chart !== undefined) {
      this.chart.destroy();
    }
  }
}

