import { Injectable } from '@angular/core';
import { Stock, TagsData } from '../../models/stock-datamodels';
import { BASEDATA_STOCKS, BASEDATA_TAGS } from './base-input-data';


@Injectable({
  providedIn: 'root'
})
export class BaseInputDataService {

  constructor() { }

  getBaseStockData(): Stock[] {
    return BASEDATA_STOCKS;
  }

  getTagsData(): TagsData[] {
    return BASEDATA_TAGS;
  }

}
