import { Injectable } from '@angular/core';
import { BaseInputDataService } from '../../constants/input-data/base-input-data.service';
import { Tagtype } from '../../constants/enums';

@Injectable({
  providedIn: 'root'
})
export class TagUtilService {
  containsTagOfType(tags: string[], tagtype: Tagtype): boolean {
    let returnBool = false;
    tags.forEach(tag => {
      if (this.isTagThisType(tag, tagtype)) {
        returnBool = true;
      }
    });
    return returnBool;
  }
  constructor(private baseDataService: BaseInputDataService) { }

  isTagThisType(tagName: string, tagType: Tagtype): boolean {
    return this.findTagTypeByName(tagName) === tagType;
  }


  findSccsColorCodeByName(key: string): string {
    let value;
    this.baseDataService.getTagsData().forEach(element => {
      if (element.tagName === key) {
        value = element.sccsColorCode;
      }
     });
    return value;
  }

  findTagTypeByName(key: string): Tagtype {
    let value: Tagtype;
    this.baseDataService.getTagsData().forEach(element => {
      if (element.tagName === key) {
        value = element.tagType;
      }
     });
    return value;
  }

}
