import { Component, Directive, EventEmitter, Input, Output, QueryList, ViewChildren, OnInit } from '@angular/core';
import { CheckboxMultiSelectDropdownComponent } from '../../shared/checkbox-multi-select-dropdown/checkbox-multi-select-dropdown.component';
import { BaseInputDataService } from 'src/app/core/constants/input-data/base-input-data.service';
import { TagsData, Stock } from 'src/app/core/models/stock-datamodels';
import { TagUtilService } from 'src/app/core/services/tag-util-service/tag-util.service';
import { Tagtype } from 'src/app/core/constants/enums';

export type SortColumn = keyof Stock | '';
export type SortDirection = 'asc' | 'desc' | '';
const rotate: {[key: string]: SortDirection} = { 'asc': 'desc', 'desc': '', '': 'asc' };
const compare = (v1: string, v2: string) => v1 < v2 ? -1 : v1 > v2 ? 1 : 0;

export interface SortEvent {
  column: SortColumn;
  direction: SortDirection;
}

@Directive({
  selector: 'th[sortable]',
  host: {
    '[class.asc]': 'direction === "asc"',
    '[class.desc]': 'direction === "desc"',
    '(click)': 'rotate()'
  }
})
export class NgbdSortableHeader {

  @Input() sortable: SortColumn = '';
  @Input() direction: SortDirection = '';
  @Output() sort = new EventEmitter<SortEvent>();

  rotate() {
    this.direction = rotate[this.direction];
    this.sort.emit({column: this.sortable, direction: this.direction});
  }
}

@Component({
  selector: 'app-table',
  templateUrl: './main-table.component.html',
  styleUrls: ['./main-table.component.scss']
})
export class TableComponent implements OnInit {

  checked = true;
  stocks: Stock[];
  showSizeTagT = true;
  showSectorTagT = true;
  showRiskTagT = true;
  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  constructor(private baseDataService: BaseInputDataService, private tagUtilService: TagUtilService) { }

  ngOnInit(): void {
    this.stocks = this.baseDataService.getBaseStockData();
  }

  onSelectChanged(eventArgs: boolean[]): void {
    this.showSizeTagT = eventArgs[0];
    this.showSectorTagT = eventArgs[1];
    this.showRiskTagT = eventArgs[2];
  }

  getTagColor(item: string) {
    return this.tagUtilService.findSccsColorCodeByName(item);
  }

  tagShouldShow(name: string) {
    let s: Tagtype;
    this.baseDataService.getTagsData().forEach(element => {
      if (element.tagName === name) {
        s = element.tagType;
      }
    });
    switch (s) {
      case Tagtype.RISK:
        return this.showRiskTagT;
        break;

      case Tagtype.SECTOR:
        return this.showSectorTagT;
        break;

      case Tagtype.SIZE:
        return this.showSizeTagT;
        break;

      default:
        return true;
        break;
    }
  }
  onSort({column, direction}: SortEvent) {

    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting countries
    if (direction === '' || column === '') {
      this.stocks = this.baseDataService.getBaseStockData();
    } else {
      this.stocks = [...this.baseDataService.getBaseStockData()].sort((a, b) => {
        let res;
        if ( /^[0-9,.,-]*$/.test(a[column].toString())) {
          const aa: number = +a[column].toString();
          const bb: number = +b[column].toString();
          res = bb - aa;
        } else {
          res = compare(`${a[column]}`, `${b[column]}`);
        }
        return direction === 'asc' ? res : -res;
      });
    }
  }
}


