import { Component, Output, EventEmitter} from '@angular/core';
import { faEye } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-checkbox-multi-select-dropdown',
  templateUrl: './checkbox-multi-select-dropdown.component.html',
  styleUrls: ['./checkbox-multi-select-dropdown.component.css']
})
export class CheckboxMultiSelectDropdownComponent {
  @Output() customChange = new EventEmitter<boolean[]>();
  faEye = faEye;
  showSizeTag = true;
  showSectorTag = true;
  showRiskTag = true;
  constructor() { }

  onValueChanged() {
    this.customChange.emit([this.showSizeTag, this.showSectorTag, this.showRiskTag]);
  }

}
